import { useMemo, useState } from "react"
import { createContainer } from "unstated-next"
import { PerpetualProtocol, PositionSide, SupportedChainIds } from "@perp/sdk-curie";
import Big from "big.js"

export const Trade = createContainer(useTrade)

function useTrade() {
    /* long = 1, short = 0 */
    const [side, setPositionSide] = useState<PositionSide>(PositionSide.LONG);

    /* min: 1, max: 10 */
    // const [leverage, setLeverage] = useState<number>(1)

    /* min: 0% */
    const [slippage, setSlippage] = useState<Big>(new Big(0.02));

    const [collateral, setCollateral] = useState<Big>(new Big(1));

    const [perpetualObject, setPerpetualObject] = useState<PerpetualProtocol>(new PerpetualProtocol({
		// chainId: SupportedChainIds.OPTIMISM_GOERLI,
		chainId: SupportedChainIds.OPTIMISM,   
		// chainId: 420,                 
		providerConfigs: [
			{
				rpcUrl: "https://mainnet.optimism.io",
                // rpcUrl: "https://opt-mainnet.g.alchemy.com/v2/NqisNVbk-thJAQJMZKXdtIy-Vlyfryj-",                
				// rpcUrl: "https://goerli.optimism.io/",								
			},
		],
	}));

    return useMemo(
        () => ({
            side,
            setPositionSide,            
            slippage,
            setSlippage,
            collateral,
            setCollateral,
            perpetualObject,            
        }),
        [side, slippage, collateral],
    )
}
