import React, { useEffect } from "react"
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Box from '@mui/material/Box';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import {AMM} from "../../../container/amm";

function MarketSelector() {
	const MarketList = ["ETHUSD", "BNBUSD", "AVAXUSD"];  	

	const {marketticker, setMarketTicker} = AMM.useContainer();
	const [open, setOpen] = React.useState(false);

  const handleChange = (event: SelectChangeEvent<typeof marketticker>) => {
    setMarketTicker(event.target.value);		
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleOpen = () => {
    setOpen(true);
  };

  return (
    <>      
      <FormControl variant="standard" sx={{ m: 1, width: '300px' }}>
        <InputLabel size="small" shrink sx={{
          left: '120px',
          color: 'rgba(145, 153, 167, 1)', 
          '&.Mui-focused': {color: 'rgba(145, 153, 167, 1)'}
        }}>
          Market
        </InputLabel>
        <Select
          labelId="demo-controlled-open-select-label"
          id="demo-controlled-open-select"
          open={open}
          defaultValue={"ETHUSD"}
          onClose={handleClose}
          onOpen={handleOpen}        
          label="Market"
          onChange={handleChange}
          sx={{'&:hover:not(.Mui-disabled):before': {borderBottom: 'none'},
            '& > svg': {color: 'white'},
            '& ul': {backgroundColor: 'rgba(9, 9, 10, 1)'},
            '&::before': {border: 'none'}, color: 'white',
            '&::after': {borderBottom: 'none'}
          }}          
        >          
					{MarketList.map((market) => (
						<MenuItem key={market} value={market} sx={{color: 'black'}}>{market}</MenuItem>	
					))}
        </Select>
      </FormControl>
    </>
  );
}

export default MarketSelector
