import { useMemo, useState } from "react"
import { createContainer } from "unstated-next"

export const AMM = createContainer(useAMM)

function useAMM() {    
    const [marketticker, setMarketTicker] = useState<string>('ETHUSD');   

    return useMemo(
        () => ({
            marketticker,
            setMarketTicker
        }),
        [marketticker, setMarketTicker],
    )
}
