import {useState, useMemo, useCallback, useEffect} from "react";

import {	
	Box,		 
	Button,
	TextField,
	Typography,
	InputAdornment	
} from "@mui/material";

import { useWeb3Context } from "../../../hooks/web3Context";
import {Trade} from '../../../container/trade';
import {AMM} from "../../../container/amm";
import { DEFAULD_NETWORK } from "../../../constants/blockchain";
import { PerpetualProtocol, PositionSide, SupportedChainIds, Wallet, big2BigNumberAndScaleUp, getTransaction, PositionDraft } from "@perp/sdk-curie";

import Big from "big.js";

function SendTransaction() {	
	const { connected, web3, address, provider, providerChainID} = useWeb3Context();  
	const [oppositeAmount, setOppositeAmount] = useState('');
	const [entryPrice, setEntryPrice] = useState('');
	const [tradingFee, setTradingFee] = useState('');
	const [priceImpact, setPriceImpact] = useState('');
	const [buyingPower, setBuyingPower] = useState('');
	const [PositionOpen, setPositionOpen] = useState(false);
	// const [perpetualObject, setPerpetualObject] = useState<PerpetualProtocol>(new PerpetualProtocol({
	// 	// chainId: SupportedChainIds.OPTIMISM_GOERLI,
	// 	chainId: SupportedChainIds.OPTIMISM,   
	// 	// chainId: 420,                 
	// 	providerConfigs: [
	// 		{
	// 			rpcUrl: "https://mainnet.optimism.io",
	// 			// rpcUrl: "https://goerli.optimism.io/",								
	// 		},
	// 	],
	// }));
	const {perpetualObject} = Trade.useContainer();
	const [newPositionDraft, setNewPositionDraft] = useState<PositionDraft<string> | undefined>();
	
	let _newPositionDraft: PositionDraft<string> | undefined

	const {side} = Trade.useContainer();
	const {marketticker} = AMM.useContainer();
	const {collateral} = Trade.useContainer();
	const {slippage} = Trade.useContainer();
	const isAmountInputBase = false;
	let tickerSymbol = marketticker;
  	let amountInput = collateral;			
	const outputSymbol = marketticker.substring(0, marketticker.length - 3);
	
	useEffect(() => {        
		async function BeforeAll() {
			if( connected && providerChainID === DEFAULD_NETWORK ) {	
				console.log("ChainId", SupportedChainIds.OPTIMISM);			
				await perpetualObject.init();
				await perpetualObject.connect({ signer: provider.getSigner() });
				// const accountValue = await perp.contractReader.getNativeBalance(address); 
				// console.log("Account Native value", accountValue);
				// const VaultBalance = await perp.contractReader.getVaultBalanceOfSettlementToken(address); 
				// console.log("Account Vault value", VaultBalance);
				// const Allowance = await perp.contractReader.getAllowanceOfSettlementToken(address, spender); 
				// console.log("Allowance value", Allowance);
				// const PositionAction = await perp.contractReader.getClearingHouseOpenPositionAction(); 
				// console.log("Position Action", PositionAction);
				_newPositionDraft = perpetualObject.clearingHouse?.createPositionDraft({
					tickerSymbol,
					side,
					amountInput,
					isAmountInputBase
				});	
				console.log("_newPosition:::", _newPositionDraft);						
				setNewPositionDraft(_newPositionDraft);						
				setEntryPrice((await _newPositionDraft!.getEntryPrice()).toFixed(2));
				setTradingFee((await _newPositionDraft!.getTransactionFee()).toFixed(8));
				setPriceImpact((await _newPositionDraft!.getPriceImpact()).toFixed(5));
				setBuyingPower((await _newPositionDraft!.getBuyingPower()).toFixed(4));
				const oppositeAmountBound = await _newPositionDraft?.getOppositeAmountBound(slippage); 
				setOppositeAmount(oppositeAmountBound!.toFixed(7));						
			}
		}
		BeforeAll();
				
	}, [connected, providerChainID, marketticker, side, collateral, slippage]); 

	const handleOpen = () => {
		perpetualObject?.clearingHouse?.openPosition(newPositionDraft!, slippage);    
		console.log("Perp openPosition", newPositionDraft);
  };	

	return (
		<Box sx={{mt: 2}}>
			<Box sx={{mb: 2}}>
				<TextField
					id="outlined-name"
					label="Output"
					type="number"
					disabled
					value={oppositeAmount}
					placeholder="0"
					InputLabelProps={{
						shrink: true,
					}}				
					InputProps={{						
						endAdornment: <InputAdornment position="end">{outputSymbol}</InputAdornment>,
					}}
					sx={{width: '100%',
						'& .MuiFormLabel-root.Mui-disabled, & .MuiFormLabel-root.Mui-focused, & .MuiTypography-root': {color: '#a3a8b7'},
						'& > div': {color: 'white'},
						'& .MuiInputBase-root.MuiOutlinedInput-root.Mui-disabled': {color: '#a3a8b7'},						
						'& .MuiInputBase-root > input': {'-webkit-text-fill-color': '#a3a8b7'},
						'& .MuiInputBase-root.MuiOutlinedInput-root.Mui-disabled .MuiOutlinedInput-notchedOutline': {borderColor: 'rgba(255, 255, 255, 0.16)'}						
					}}					
				/>
			</Box>
			<Box sx={{mx: 'auto', display: 'flex', justifyContent: 'space-between', pb: 1}}>
				<Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>Buying Power</Typography>
				<Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>${buyingPower}</Typography>
			</Box>
			<Typography sx={{mx: 'auto', py: 1, textAlign: 'left', color: 'white'}}>
				Summary
			</Typography>
			<Box sx={{mx: 'auto', display: 'flex', justifyContent: 'space-between', pb: 1}}>
				<Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>Entry Price</Typography>
				<Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>${entryPrice}</Typography>
			</Box>
			<Box sx={{mx: 'auto', display: 'flex', justifyContent: 'space-between', pb: 1}}>
				<Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>Trading Fee</Typography>
				<Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>${tradingFee}</Typography>
			</Box>
			<Box sx={{mx: 'auto', display: 'flex', justifyContent: 'space-between', pb: 1}}>
				<Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>Price Impact</Typography>
				<Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>{priceImpact}%</Typography>
			</Box>
			<Button variant="contained" 
				disabled={connected === false}
				sx={{width: '100%', paddingY: '12px', 
					backgroundColor: side === PositionSide.LONG? 'success.light': '#ef5350', 
					display: 'block',										
					mx: 'auto',
					'&: hover': {backgroundColor: side === PositionSide.LONG? 'success.main': '#f44336'},
					'&: disabled': {backgroundColor: side === PositionSide.LONG? 'success.light': '#ef5350', color: 'black', opacity: 0.8} 
				}}
				onClick={handleOpen}			
			>
				Confirm {side}
			</Button>			
		</Box>
	)
}

export default SendTransaction;