import * as React from 'react';
import {useState, useMemo, useCallback, useEffect} from "react";

import {	
	Box,	
	Button,	   
	Typography,
  Tabs,
  Tab
} from "@mui/material";

import { useWeb3Context } from "../../hooks/web3Context";
import {Trade} from '../../container/trade';
import {AMM} from "../../container/amm";

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ py: 2 }}>
          {children}
        </Box>
      )}
    </div>
  );
}

function a11yProps(index: number) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

export default function Positions() {
  const { connected, web3, address, provider, providerChainID} = useWeb3Context(); 
  const [value, setValue] = useState(0);
  const [isLoss, setLoss] = useState(false);
  const [isPositionOpened, setPositionOpened] = useState(false);
  const [posSize, setPositionSize] = useState<string>('')
  const [side, setSide] = useState<string>('');
  const [pendingValue, setPendingValue] = useState<string>('');
  const [openPrice, setOpenPrice] = useState<string>('');
  const [indexPrice, setIndexPrice] = useState<string>('');
  const [liquidationPrice, setLiquidationPrice] = useState<string>('');
  const [marginRatio, setMarginRatio] = useState<string>('');
  const [leverage, setLeverage] = useState<string>('');
  
  const {perpetualObject} = Trade.useContainer();
  const {slippage} = Trade.useContainer();
  const {marketticker} = AMM.useContainer();
  let tickerSymbol = marketticker;
  const outputSymbol = marketticker.substring(0, marketticker.length - 3);
  const hasconnected = perpetualObject.hasConnected();  

  useEffect(() => {
    const getPositionInfo = async() => {      
      if(connected) {        
        await perpetualObject.init();
				await perpetualObject.connect({ signer: provider.getSigner() });
        const position = await perpetualObject?.positions?.getTakerPositionByTickerSymbol(tickerSymbol);   
        if(position !== undefined) {
          setPositionOpened(true);
        }
        const _posSize = position!.sizeAbs.toFixed(8);
        setPositionSize(_posSize);
        const _side = position!.side;
        setSide(_side);     
        const pendingValue_ = await perpetualObject?.positions?.getTotalPendingFundingPayments();
        console.log("Pending Value_", pendingValue_![tickerSymbol].s);
        if(pendingValue_![tickerSymbol].s !== -1) {
          setLoss(false);
        } else {
          setLoss(true);          
        }
        setPendingValue(pendingValue_![tickerSymbol].toFixed(4));        
        const _openPrice = position!.entryPrice.toFixed(2);
        const _indexPrice = await perpetualObject.contractReader.getIndexPrice(position!.market.baseAddress); 
        // console.log("Indexprice", _indexPrice);
        const _liquidationPrice = position!.liquidationPrice; 
        setOpenPrice(_openPrice);
        setIndexPrice(_indexPrice.toFixed(2));
        setLiquidationPrice(_liquidationPrice!.toFixed(1));
        console.log("Position num", position);
        const _marginRatio = await perpetualObject?.positions?.getAccountMarginRatio();
        const _leverage = await perpetualObject?.positions?.getAccountLeverage();
        setMarginRatio((Number(_marginRatio!.toFixed(4))*100).toFixed(1));   
        setLeverage(_leverage!.toFixed(2));     
      }
    }
    getPositionInfo();    
  }, [connected])

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };

  const handleClose = async () => {
		const position = await perpetualObject?.positions?.getTakerPositionByTickerSymbol(tickerSymbol);
		if(position !== undefined) {			
			await perpetualObject?.clearingHouse?.closePosition(position!, slippage) 
		} else {			
		};	
  };

  return (
    <Box sx={{ width: '100%' }}>
      <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Tabs value={value} onChange={handleChange} aria-label="basic tabs example" textColor='primary'
          TabIndicatorProps={{
            style: {
              backgroundColor: "white"              
            }
          }}
            sx={{ 
              '& .MuiTab-root': {color: '#a3a8b7'},               
              '& .MuiTab-root.Mui-selected': {color: 'white'}          
            }}
        >
          <Tab label="Position" {...a11yProps(0)} />
          <Tab label="Limit Orders" {...a11yProps(1)} />
          <Tab label="Fills" {...a11yProps(2)} />
        </Tabs>
      </Box>
      <TabPanel value={value} index={0}>        
        {isPositionOpened &&
          <Box sx={{maxWidth: '360px'}}>
            <Box sx={{display: 'flex', justifyContent: 'space-between', py: 1}}>
              <Box sx={{display: 'flex', justifyContent: 'flex-start', alignItems: 'center'}}>              
                <Typography sx={{fontSize: '16px', mr: 1, color: 'white'}}>{posSize}</Typography>
                <Typography sx={{fontSize: '12px', color: '#a3a8b7'}}>{outputSymbol}</Typography>
              </Box>
              <Typography sx={{fontSize: '14px', color: side==='LONG'? 'rgba(94, 217, 151, 1)': 'rgba(234, 98, 98, 1)'}}>{side}</Typography>
            </Box>
            <Box sx={{display: 'flex', justifyContent: 'space-between', py: 1}}>
              <Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>Pending Funding Payment</Typography>
              <Typography sx={{fontSize: '14px', color: isLoss===true? 'rgba(234, 98, 98, 1)': 'rgba(94, 217, 151, 1)'}}>{pendingValue}</Typography>
            </Box>
            <Box sx={{display: 'flex', justifyContent: 'space-between', py: 1}}>
              <Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>Avg. Open Price</Typography>
              <Typography sx={{fontSize: '14px', color: 'white'}}>{openPrice}</Typography>
            </Box>
            <Box sx={{display: 'flex', justifyContent: 'space-between', py: 1}}>
              <Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>Index Price</Typography>
              <Typography sx={{fontSize: '14px', color: 'white'}}>{indexPrice}</Typography>
            </Box>
            <Box sx={{display: 'flex', justifyContent: 'space-between', py: 1}}>
              <Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>Liquidation Price</Typography>
              <Typography sx={{fontSize: '14px', color: 'white'}}>{liquidationPrice}</Typography>
            </Box>
            <Box sx={{display: 'flex', justifyContent: 'space-between', py: 1}}>
              <Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>Margin Ratio</Typography>
              <Typography sx={{fontSize: '14px', color: 'white'}}>{marginRatio}%</Typography>
            </Box>
            <Box sx={{display: 'flex', justifyContent: 'space-between', py: 1}}>
              <Typography sx={{fontSize: '14px', color: '#a3a8b7'}}>Leverage</Typography>
              <Typography sx={{fontSize: '14px', color: 'white'}}>{leverage}x</Typography>
            </Box>
            <Box sx={{display: 'flex'}}>
              <Button variant="contained" 
                disabled={connected === false}
                sx={{paddingY: '8px', mt: 1,
                  backgroundColor: 'rgb(255, 180, 89)', 
                  color: 'black',
                  '&: hover': {backgroundColor: 'rgb(255, 180, 89)', color: 'white'},
                  '&: disabled': {backgroundColor: 'gray', color: 'black', opacity: 0.8} 
                }}
                onClick={handleClose}			
              >
                Close
              </Button>
            </Box>
          </Box>
        }
      </TabPanel>
      <TabPanel value={value} index={1}>
        Limit Orders
      </TabPanel>
      <TabPanel value={value} index={2}>
        Fills
      </TabPanel>
    </Box>
  );
}