import {useState, useMemo, useCallback, useEffect} from "react";

import {	
	Box,		 
	TextField,
	InputAdornment	
} from "@mui/material";

import Big from "big.js"

import {Trade} from '../../../container/trade';
import { USDC_PRECISION } from "../../../constants/number";
import { formatInput } from "../../../util/format"

import { useDebounce } from "../../../hooks/useDebounce"

function Collateral() {
	const {collateral, setCollateral} = Trade.useContainer();	
	const [_collateral, _setCollateral] = useState<string>("");	

	const debouncedCollateral = useDebounce({ value: _collateral, delay: 800 });
	
	const handleOnInput = useCallback(
		e => {
			const value = e.target.value;
			if (value >= 0) {
				const formattedValue = formatInput(value, USDC_PRECISION);
				_setCollateral(formattedValue);
			}
		},
		[_setCollateral],
	)

	useEffect(() => {
		/* reset collateral to null */
		if (debouncedCollateral === "") {
			// setCollateral(null);
			return;
		}
		/* detect if the value is different */
		const b_debouncedCollateral = new Big(debouncedCollateral);
		if (!collateral?.eq(b_debouncedCollateral)) {
			setCollateral(b_debouncedCollateral);
		}
	}, [collateral, debouncedCollateral, setCollateral]);
	

	return useMemo(
		() => (
		<Box sx={{my: 2}}>
			<TextField
				id="outlined-name"
				label="Collateral"
				type="number"
				value={_collateral}
				placeholder="1"
				InputLabelProps={{
					shrink: true,
				}}				
				InputProps={{
					endAdornment: <InputAdornment position="end">USDC</InputAdornment>,
				}}
				onChange={handleOnInput}
				sx={{width: '100%',
					'& > label, & .MuiFormLabel-root.Mui-focused': {color: '#a3a8b7'},
					'& > div': {color: 'white'},
					'& .MuiInputBase-root.Mui-focused .MuiOutlinedInput-notchedOutline': {
						borderColor: '#a3a8b7',
						borderWidth: '1px'						
					},
					'& .MuiInputBase-root:hover .MuiOutlinedInput-notchedOutline': {
						borderColor: '#a3a8b7'						
					},
					'& .MuiTypography-root': {color: '#a3a8b7'},
					'& fieldset.MuiOutlinedInput-notchedOutline': {borderColor: 'rgba(255, 255, 255, 0.16)'}
				}}
			/>
		</Box>		
		), [_collateral, handleOnInput]
	)
}

export default Collateral;