import {useState, useCallback, useEffect} from "react";

import {
	AppBar,
	Box,
	ButtonGroup,
	Button,
	Container,    
	Typography,
} from "@mui/material";

import { PositionSide } from "@perp/sdk-curie";
import {Trade} from '../../../container/trade';


function SideSwitcher() {
	const {side, setPositionSide} = Trade.useContainer();	
	const handleLongOnClick = useCallback(() => {
		if (side !== PositionSide.LONG) {
			setPositionSide(PositionSide.LONG);			
		}		
	}, [setPositionSide, side])

	const handleShortOnClick = useCallback(() => {
		if (side !== PositionSide.SHORT) {
			setPositionSide(PositionSide.SHORT);			
		}				
	}, [setPositionSide, side])	

	return (
		<ButtonGroup size="large" sx={{py: '12px', border: 'none', width: '100%'}}>
			<Button 
				sx={{
					width: '50%',					
					fontSize: '12px',
					backgroundColor: 
						side === PositionSide.LONG? '#52c41a' : 'gray',
					color: 
						side === PositionSide.LONG? 'white' : 'black',
					border: '1px solid transparent',
					'&: hover': {
						backgroundColor: '#49bc10',	
						border: '1px solid transparent'
					}				
				}}
				onClick={handleLongOnClick}
			>
				Long
			</Button>
			<Button 
				sx={{
					width: '50%',					
					fontSize: '12px',
					backgroundColor: 
						side === PositionSide.SHORT? '#fa672d' : 'gray',
					color: 
						side === PositionSide.SHORT? 'white' : 'black',
					border: '1px solid transparent',
					'&: hover': {
						backgroundColor: '#f85819',	
						border: '1px solid transparent'					
					}		
				}}
				onClick={handleShortOnClick}
			>
				Short
			</Button>			
		</ButtonGroup>

		
	)
}

export default SideSwitcher;